.navigation ul
{
  
  display: flex;
  font-size: 190%;
  align-items: center;
  padding-right: 1em;
  
}

.navigation ul li 
{
  
  list-style: none;
  width: 500px;
  height: 100px;
  color: black;
  position: relative;
  text-align: center;
  
  
  
}



.navigation ul li a
{
  
  position:relative;
  /* display:inline-block; */
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: black;
  text-align: center;
  font-size: 60%;
  
  
  
  
  
}

.navigation ul li a:hover
{
  position:relative;
  display:inline-block;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  color: rgb(25, 33, 138);
  text-align: center;
  
}


.navigation ul li .icon
{
  position: relative;
  line-height: 30px;
  transition: 0.5s;
  font-size:300%;
  text-align: center;
  
  
  
}



.navigation ul li .icon:hover
{
  vertical-align: top;
  line-height: 30px;
  transition: 0.5s;
  font-size:150px;
  
}






